body {
  margin: 0;
  background-color: white;
}

.mainPage {
  width: 100%;
  height: 100%;
  position: absolute;
  display: flex;
  flex-direction: column;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: flex-start;
  background-color: #edf2f4;
  overflow: auto;
}

.start {
  background: rgba(175, 0, 0, 0.9);
  font-size: 16px;
  padding: 10px 20px;
  color: white;
  border: none;
  border-radius: 15px;
}
